.wrapperBox {
  min-height: 100vh;
  background-color: #f1f2f3;
  justify-content: center;
  align-items: center;
  
}
.wrapperSidebar {
  min-height: 100vh;
}
.widgetContainer {
  margin: 0;
  width: 100%;
  border-radius: var(--radius);
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin: 0 auto;
  min-height: 550px;
  max-height: calc(100vh - 70px);
}
.wrapperBox .widgetContainer{
  background-color: #fff;
  max-height: 550px;
}
.wrapperSidebar .widgetContainer{
  max-width: 100%;
  min-height: 100%;
  max-height: 100%;
  height: 100vh;
}

.widgetContainerAdaptiveHeight {
  height: unset;
}

.widgetContainerWithHeader {
  padding: 0;
}

.navigationHeader {
  min-height: 67px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  padding: 18px 20px;
  border-bottom: 1px solid var(--nav-header-border);
  background: var(--bg-dark);
  z-index: 1;
}

.headingContainer {
  flex: 1;
}

.heading {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.widgetContainerBody {
  overflow-x: hidden;
  padding: 15px;
  margin-bottom: 15px;
  flex: 1;
}
.wrapperSidebar .widgetContainer .widgetContainerBody{
  flex: unset;
  height: calc(100vh - 70px);
  overflow-y: auto;
  flex: 1;
}

.backIcon {
  margin-right: 5px;
  cursor: pointer;
}

.footer {
  position: sticky;
  bottom: 0;
  padding: 15px;
}

.hasFooter {
  flex-direction: column;
}