@font-face {
  font-family: 'icomoon';
  src:  url('./icomoon.eot?mm11v3');
  src:  url('./icomoon.eot?mm11v3#iefix') format('embedded-opentype'),
    url('./icomoon.ttf?mm11v3') format('truetype'),
    url('./icomoon.woff?mm11v3') format('woff'),
    url('./icomoon.svg?mm11v3#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

@font-face {
  font-family: 'icomoonMenu';
  src:  url('./icomoon-menu.eot?mm11v3');
  src:  url('./icomoon-menu.eot?mm11v3#iefix') format('embedded-opentype'),
    url('./icomoon-menu.ttf?mm11v3') format('truetype'),
    url('./icomoon-menu.woff?mm11v3') format('woff'),
    url('./icomoon-menu.svg?mm11v3#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="iconMenu-"], [class*=" iconMenu-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoonMenu' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

