@import "./assets/fonts/Icomoon/icomoon.css";
@font-face {
  font-family: 'SF-Pro-Display-Regular';
  src: url(./assets/fonts/SF-Pro-Display/SF-Pro-Display-Regular.woff) format('woff');
  font-weight: 400
}
@font-face {
  font-family: 'SF-Pro-Display-Medium';
  src: url(./assets/fonts/SF-Pro-Display/SF-Pro-Display-Medium.woff) format('woff');
  font-weight: 500
}
@font-face {
  font-family: 'SF-Pro-Display-Bold';
  src: url(./assets/fonts/SF-Pro-Display/SF-Pro-Display-Bold.woff) format('woff');
  font-weight: 700
}
@font-face {
  font-family: 'Roboto-Regular';
  src: url(./assets/fonts/Roboto/Roboto-Regular.woff) format('woff');
  font-weight: 400
}
@font-face {
  font-family: 'Roboto-Medium';
  src: url(./assets/fonts/Roboto/Roboto-Medium.woff) format('woff');
  font-weight: 500
}
@font-face {
  font-family: 'Roboto-Bold';
  src: url(./assets/fonts/Roboto/Roboto-Bold.woff) format('woff');
  font-weight: 700
}
@font-face {
  font-family: 'Lato-Regular';
  src: url(./assets/fonts/Lato/Lato-Regular.woff) format('woff');
  font-weight: 400
}
@font-face {
  font-family: 'Lato-Medium';
  src: url(./assets/fonts/Lato/Lato-Medium.woff) format('woff');
  font-weight: 500
}
@font-face {
  font-family: 'Lato-Bold';
  src: url(./assets/fonts/Lato/Lato-Bold.woff) format('woff');
  font-weight: 700
}
body, html {
  height: 100%;
  overflow: hidden;
  background-color: transparent !important;
}
.PhoneInputInput {
  outline: none !important;
  background-color: transparent;
}
/* Common CSS */
.moduleLoaderSection {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 1000000;
}
[role="button"] {
  cursor: pointer;
}
.chakra-checkbox .chakra-checkbox__control {
  border-width: 1px;
}
.navContainer {
  min-height: 67px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  padding: 18px 20px;
  border-bottom: 1px solid #edeff1;
  z-index: 1;
}

select {
  cursor: pointer;
}

:focus-visible {
  outline: none !important;
}

.cardIframeWrapper iframe {
	width: 100%;
	height: 25px;
}

.country-input input {
  text-align: inherit;
}

.text-dark {
  color: #141414 !important;
}

.text-light {
  color: #767676 !important;
}

.theme-dark .text-dark {
  color: #ffffff !important;
}

.theme-dark .text-light {
  color: #9f9fa4 !important;
}

select.chakra-select > option, select > option{
  appearance:none;
    -moz-appearance:none;
    -webkit-appearance:none;
  background: var(--input-background-color);
  color: var(--input-color);
}
.theme-dark .sidebar{
  border-color: #444447;
  background: #2c2c2e;
}